import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys } from "../../../stores/Utils";

import "./dashboard.css"
import { getById_notary, get_print_structure } from "../services/fetch_service";




const ClientItem  = ({ data, elmId, openUpdateView }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			// let frm2 = {"firstName":"Belisario","lastName":"Gonzalez Arevalo","genre":"Male","dateOfBirth":"06/02/1976","placeOfBirth":{"city":"Holguin","state":"Holguin"},"currentAddress":{"No":"4307","street":"Estate Dr","city":"Louisville","state":"Kentucky","zipcode":"40216"},"dob":202536000000}
			//updStore(frmNm, frm2);
			set_obs(gen16CodeId())
		}
	});

	const openUpdView = async () => {
		openUpdateView(true)
	}

	const getPdfStruct = async () => {
		get_print_structure(openUpdView)
	}

	const update = async () => {
		getById_notary(data?.clientNotaryId,getPdfStruct)
	}

	//console.log("data",data)
	return (
		<div className="client_item_box" onClick={update}>
			<div className="_dsplFlx">
				<div className="name" >
					{`${data?.firstName} ${data?.lastName}`}
				</div>
				<div className="flexSpace"/>
				<div className="name">
					{data?.phoneNumber}
				</div>
			</div>
		</div>
	)
}

export default ClientItem;